import '../styles/AdminMenu.css';
import { Link } from 'react-router-dom'

const UserMenu = () => {
  return (
    <>
      <div className='admin_page_left'>
        <h1> Your Pannel </h1>
        <div className='admin_page_left_btn'>
          <Link to="/profile/user/userprofile">
            <button className='btns'>
              Edit Profile
            </button>
          </Link>
          <Link to="/profile/user/orders">
            <button className='btns'>
              Orders
            </button>
          </Link>
        </div>
      </div>
    </>
  )
}

export default UserMenu